import { Button } from "@/components/ui/button";
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Plus } from "lucide-react";
import Link from "next/link";

const notes = [
  {
    id: "1",
    title: "DSA Notes",
    updatedAt: "2 hours ago",
  },
  {
    id: "2",
    title: "ML Project Ideas",
    updatedAt: "Yesterday",
  },
];

export default function DashboardPage() {
  return (
    <div className="container mx-auto px-6 py-8">
      {/* Header */}
      <div className="mb-8 flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Your Notes</h1>
          <p className="text-muted-foreground">
            Create and manage your notes in one place
          </p>
        </div>

        <Button className="gap-2">
          <Plus className="h-4 w-4" />
          New Note
        </Button>
      </div>

      {/* Notes Grid */}
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {/* Add New Note Card */}
        <Card className="flex cursor-pointer items-center justify-center border-dashed transition hover:border-primary">
          <CardContent className="flex flex-col items-center justify-center py-16 text-muted-foreground">
            <Plus className="mb-2 h-6 w-6" />
            <span className="font-medium">Add New Note</span>
          </CardContent>
        </Card>

        {/* Existing Notes */}
        {notes.map((note) => (
          <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
            <Card className="h-full cursor-pointer transition hover:shadow-lg">
              <CardHeader>
                <CardTitle className="line-clamp-1">
                  {note.title}
                </CardTitle>
                <CardDescription>
                  Last updated {note.updatedAt}
                </CardDescription>
              </CardHeader>

              <CardContent>
                <Badge variant="secondary">Personal</Badge>
              </CardContent>
            </Card>
          </Link>
        ))}
      </div>
    </div>
  );
}
